function guestDropdown(){
$(document).ready(function () {
  $(".guest-dropdown").iqDropdown({
    setSelectionText: function (itemCount, totalItems) {
      let t = ["", "", ""];
      let returnString='';
      let guestsCount=itemCount['item1'] + itemCount['item2'];
      if (totalItems == 0) return " ";
      if (totalItems == 1) return "1 ";
      if ((guestsCount > 1)&&  (guestsCount < 5)) 
        returnString += `${itemCount['item1'] + itemCount['item2']} `; 
        if (guestsCount > 4) returnString += `${itemCount['item1'] + itemCount['item2']} `;
      if (itemCount['item3']==1) returnString +=`, ${itemCount['item3']} `;
      if (itemCount['item3']>1) returnString +=`, ${itemCount['item3']} `;
      return returnString;
    },
  });
});
}

export {guestDropdown};